$actions-height: 100px;

body {
	margin: 0;
	font-family: 'Comfortaa';
}

.component {
	background: #eee;
	height: 100vh;
}

.template {
	height: calc(100vh - #{$actions-height});

	max-width: 1200px;
	margin: 0 auto;

	position: relative;
	overflow: hidden;

	// border: 1px solid black;
}

.actions {
	height: $actions-height;

	display: flex;
	align-items: center;
	justify-content: center;
}

$offset: .08;
$step: 0.04;
$dots-count: 20;

$half: $dots-count / 2;
@for $i from 1 through $dots-count {
	.dot:nth-child(#{$i}) {
		$transition: '';

		@if ($i > $half) {
			$transition: #{($i + $half) * $step + $offset}s;
		} @else {
			$transition: #{$i * $step * 2 + $offset}s;
		}	

		transition: $transition transform ease-in-out;
		transform: translate3d(0, 0, 0);
	}
}

.dot {
	height: 20px;
	width: 20px;

	background: #000;
	border-radius: 50%;

	will-change: transform;
	transition: .65s transform ease;

	position: absolute;
}

.device-info {
	position: fixed;
	left: 0;
	top: 0;
	right: 0;
	bottom: 0;

	background: lighten(lightblue, 17);
	text-align: center;
	display: none;

	align-items: center;
	padding: 0 5px;
}

@media screen and (orientation: portrait) {
    .device-info {
		display: flex;
		justify-content: center;
	}

	.template {
		display: none;
	}
}
